  font-family: 'Raleway';
  font-style: normal;
  font-weight: 300;
  src: local('Raleway Light'), local('Raleway-Light'), url('../fonts/Raleway-v4020-Light.woff2') format('woff2'), /* Super Modern Browsers */ url('../fonts/Raleway-v4020-Light.woff') format('woff'), /* Modern Browsers */ url('../fonts/Raleway-v4020-Light.otf') format('opentype') /* Safari, Android, iOS */;
}
/* raleway-300italic - latin */
