body {
  width: 100%;
  height: 100%;
  background-color: white;
}
img,
video {
  max-width: 100%;
  height: auto;
}
textarea {
  resize: vertical;
}
/* :focus {
  outline: none;
} */
input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}
select {
  border-radius: 0;
}
input::-ms-clear,
input::-ms-reveal {
  display: none;
}
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}
input[type="number"] {
  appearance: textfield;
}
input:-webkit-autofill {
  -webkit-text-fill-color: black;
  box-shadow: inset 0 0 0 1000px white;
}
select,
textarea,
input:matches([type="email"],
  [type="number"],
  [type="password"],
  [type="search"],
  [type="tel"],
  [type="text"],
  [type="url"]) {
  appearance: none;
}
body {
  font-family: Raleway, Arial, sans-serif;
  font-size: 17px;
  line-height: 1.47;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
body main {
  flex-grow: 1;
}
.container {
  box-sizing: border-box;
  margin: 0 auto;
  max-width: 1480px;
}
.list-reset {
  list-style-type: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
}
.btn-reset {
  padding: 0;
  line-height: inherit;
  background: transparent;
  border: none;
  cursor: pointer;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.scroll-lock {
  position: fixed;
  overflow-y: auto;
}
.burger {
  position: relative;
  display: inline-block;
  width: 42px;
  height: 24px;
  cursor: pointer;
}
.burger__inner {
  position: absolute;
  top: auto;
  bottom: 0;
  display: block;
  width: inherit;
  height: 4px;
  margin-top: -2px;
  border-radius: 2px;
  background-color: #ffffff;
  transition: top 0.12s cubic-bezier(0.33333, 0.66667, 0.66667, 1) 0.2s, transform 0.13s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transition-delay: 0.13s;
  transition-timing-function: ease;
  transition-duration: 0.15s;
  transition-property: transform;
}
.burger__inner::after,
.burger__inner::before {
  content: "";
  position: absolute;
  display: block;
  width: inherit;
  height: inherit;
  border-radius: inherit;
  background-color: inherit;
  transition: transfrom 0.15s;
}
.burger__inner::before {
  top: -10px;
  transition: top 0.12s cubic-bezier(0.33333, 0.66667, 0.66667, 1) 0.2s, transform 0.13s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
.burger__inner::after {
  top: -20px;
  transition: top 0.2s cubic-bezier(0.33333, 0.66667, 0.66667, 1) 0.2s, opacity 0.1s linear;
}
.active .burger__inner {
  transform: translate3d(0, -10px, 0) rotate(-45deg);
  transition-delay: 0.22s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}
.active .burger__inner::before,
.active .burger__inner::after {
  top: 0px;
}
.active .burger__inner::after {
  opacity: 0;
  transition: top 0.2s cubic-bezier(0.33333, 0, 0.66667, 0.33333), opacity 0.1s linear 0.22s;
}
.active .burger__inner::before {
  transform: rotate(-90deg);
  transition: top 0.1s cubic-bezier(0.33333, 0, 0.66667, 0.33333) 0.16s, transform 0.13s cubic-bezier(0.215, 0.61, 0.355, 1) 0.25s;
}
.top-menu {
  display: flex;
  background-color: white;
}
.top-menu__left-menu {
  position: relative;
  display: flex;
}
.top-menu__left-menu-burger {
  display: flex;
  width: 144px;
  height: 147px;
  border: 1px solid white;
  outline: none;
  background-color: #a1322c;
}
.top-menu__left-menu-burger .burger {
  margin: auto;
}
.top-menu__left-menu-overlay {
  display: none;
}
.top-menu__left-menu.active .top-menu__left-menu-overlay {
  display: block;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.top-menu__left-menu-list {
  position: absolute;
  top: 0;
  left: 100%;
  z-index: 1;
  display: none;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
  padding: 25px;
  min-width: 250px;
  min-height: 100%;
  background-color: inherit;
  border: 1px solid white;
  border-left: 0;
  background-color: #a1322c;
}
.top-menu__left-menu.active .top-menu__left-menu-list {
  display: flex;
}
.top-menu__left-menu-burger:hover .top-menu__left-menu-list,
.top-menu__left-menu-burger:focus .top-menu__left-menu-list {
  display: flex;
}
.top-menu__left-menu-link {
  color: white;
  text-decoration: none;
}
.top-menu__left-menu-link:hover,
.top-menu__left-menu-link:focus {
  text-decoration: underline;
}
.top-menu__nav {
  display: flex;
  flex-grow: 1;
  align-items: center;
}
.top-menu__nav-logo {
  display: flex;
  flex-shrink: 0;
  padding-left: 35px;
  padding-right: 35px;
  height: 144px;
  opacity: 1;
  transition: opacity 0.5s;
}
.top-menu__nav-logo img {
  display: block;
  margin: auto;
  max-height: 100%;
}
.top-menu__nav-logo:hover,
.top-menu__nav-logo:focus {
  opacity: 0.5;
}
.top-menu__nav-logo:active {
  opacity: 0.7;
}
.top-menu__nav-list {
  display: flex;
  flex-wrap: wrap;
}
.top-menu__nav-item {
  margin-left: 28px;
  padding: 10px 0;
}
.top-menu__nav-link {
  position: relative;
  display: block;
  padding-top: 2px;
  padding-bottom: 2px;
  font-size: 14px;
  font-weight: bold;
  line-height: 1;
  text-transform: uppercase;
  text-decoration: none;
  color: black;
}
.top-menu__nav-link::before,
.top-menu__nav-link::after {
  content: "";
  position: absolute;
  bottom: 0;
  display: block;
  width: 0;
  height: 2px;
  background-color: rgba(0, 0, 0, 0.3);
  transition: width 0.3s;
}
.top-menu__nav-link::before {
  right: 50%;
}
.top-menu__nav-link::after {
  left: 50%;
}
.top-menu__nav-link:active,
.top-menu__nav-link .active {
  color: #a1322c;
}
.top-menu__nav-link:active::before,
.top-menu__nav-link:active::after,
.top-menu__nav-link .active::before,
.top-menu__nav-link .active::after {
  background-color: rgba(161, 50, 44, 0.3);
}
.top-menu__nav-link:hover::before,
.top-menu__nav-link:focus::before,
.top-menu__nav-link:hover::after,
.top-menu__nav-link:focus::after {
  width: 50%;
}
.top-menu__nav-language {
  display: flex;
  flex-shrink: 0;
  justify-content: space-between;
  align-items: center;
  margin-left: 42px;
}
.top-menu__nav-language-btn {
  margin-left: 12px;
}
.top-menu__nav-toggle {
  display: none;
}
@media screen and (max-width: 1024px) {
  .top-menu {
    position: relative;
  }
  .top-menu__left-menu {
    position: absolute;
    top: 0;
    left: 20px;
  }
  .top-menu__left-menu-list {
    background-color: white;
    border: 2px solid #a1322c;
  }
  .top-menu__left-menu-link {
    color: #a1322c;
  }
  .top-menu__left-menu-burger {
    background-color: transparent;
    border: 0;
  }
  .top-menu__left-menu-burger .burger__inner {
    background-color: #a1322c;
  }
  .top-menu__nav {
    flex-direction: column;
  }
  .top-menu__nav-language {
    position: absolute;
    top: 56px;
    right: 20px;
  }
  .top-menu__nav-logo {
    padding-left: 150px;
    padding-right: 150px;
  }
}
@media screen and (max-width: 768px) {
  .top-menu__nav-list {
    position: absolute;
    right: 0;
    display: none;
    min-width: 50vw;
    text-align: center;
    padding-top: 50px;
    padding-bottom: 50px;
    padding-right: 50px;
    padding-left: 50px;
    background-color: white;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
  }
  .top-menu__nav.active .top-menu__nav-list {
    display: block;
  }
  .top-menu__nav-item {
    margin: 0;
  }
  .top-menu__nav-logo {
    padding-left: 55px;
    padding-right: 55px;
  }
  .top-menu__nav-language {
    display: none;
  }
  .top-menu__nav-link {
    display: inline-block;
  }
  .top-menu__left-menu {
    display: none;
  }
  .top-menu__nav-toggle {
    display: block;
    position: absolute;
    right: 20px;
    top: 20px;
  }
  .top-menu__nav-toggle-burger {
    outline: none;
  }
  .top-menu__nav-toggle-burger .burger__inner {
    background-color: #a1322c;
  }
  .top-menu__nav-toggle-overlay {
    display: none;
  }
}
.footer {
  background-color: #1f1f1f;
}
.footer__inner {
  display: flex;
}
.footer__left {
  background-color: #ac0000;
  padding: 38px 52px;
}
.footer__center {
  background-color: black;
}
.footer__right {
  display: flex;
  flex-grow: 1;
  padding: 42px 50px 0 65px;
}
.footer__logo {
  display: block;
  opacity: 1;
  transition: opacity 0.5s;
}
.footer__logo:hover,
.footer__logo:focus {
  opacity: 0.5;
}
.footer__logo:active {
  opacity: 0.7;
}
.footer__contacts {
  font-size: 17px;
  color: white;
}
.footer__contacts a[href^="tel:"] {
  font-size: 25px;
  color: inherit;
  text-decoration: none;
}
.footer__btn {
  box-sizing: border-box;
  width: 217px;
  padding: 15px;
  color: white;
  border: 1px solid white;
  border-radius: 28px;
  outline: none;
  transition: color 0.3s, background-color 0.3s;
}
.footer__btn:hover,
.footer__btn:focus {
  background-color: white;
  color: #a1322c;
}
.footer__social-list {
  padding: 18px 45px;
}
.footer__social-item {
  margin-top: 23px;
}
.footer__social-link {
  display: block;
  filter: brightness(0) invert(1);
  transition: filter 0.5s;
}
.footer__social-link img {
  display: block;
}
.footer__social-link:hover,
.footer__social-link:focus {
  filter: brightness(1) invert(0);
}
.footer__right-links {
  margin-left: auto;
  text-align: right;
}
.footer__link {
  display: block;
  font-size: 14px;
  color: white;
  text-decoration: none;
  transition: color 0.3s;
}
.footer__link:hover,
.footer__link:focus {
  color: #a1322c;
  text-decoration: underline;
}
.footer__link {
  margin-top: 10px;
}
.footer__right-links .footer__link {
  font-size: 13px;
  text-decoration: underline;
}
.footer__link--offer {
  margin: 55px 0;
}
@media screen and (max-width: 1024px) {
  .footer__inner {
    flex-wrap: wrap;
  }
  .footer__left {
    width: 50%;
    box-sizing: border-box;
  }
  .footer__center {
    width: 50%;
  }
  .footer__rigth {
    width: 100%;
    box-sizing: border-box;
  }
  .footer__social-list {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100%;
    box-sizing: border-box;
  }
  .footer__social-item {
    margin-top: 0;
  }
  .footer__social-item ~ .footer__social-item {
    margin-left: 10px;
  }
}
@media screen and (max-width: 768px) {
  .footer__inner {
    flex-direction: column;
  }
  .footer__left {
    width: 100%;
    text-align: center;
    padding: 38px 12px;
  }
  .footer__center {
    width: 100%;
  }
  .footer__right {
    display: block;
    padding: 20px;
  }
  .footer__left-links {
    display: flex;
    flex-wrap: wrap;
  }
  .footer__right-links {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 30px;
    text-align: left;
  }
  .footer__link {
    margin-top: 10px;
    margin-bottom: 5px;
    margin-right: 20px;
  }
}
